@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

:root{
  --verde: #00bfa5;
}

header{
  height: 225px;
  width: 100%;

  padding-top: 27px;

  background-color: #00bfa5;

  background-color: var(--verde);

  z-index: -1;

}


#icon-div{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;

  padding-left: 37px;
}

#whatsapp-icon{
  width: 40px;
  height: 40px;
}

#text-whats-icon{
  width: 1050px;

  margin-left: 14px;

  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  color: rgba(255,255,255);
}

@media (min-width: 510px){
  #icon-div{
    -webkit-justify-content: center;
            justify-content: center;
    justify-items: center;
  }
}
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;

  font-family: 'Roboto', sans-serif;
    font-weight: 100;
}

body{
  background-color: rgba(0,0,0,.2);
}

/*
t - tutorial.js
q - qrcode.js
li - link e input
tv - tutorial_video.js
*/

#container{
  max-width: 1000px;
  width: 92%;
  height: 926px;
  
  background-color: white;

  margin: -85px auto 97px;

  box-shadow: 0px 8px 26px -7px rgba(0,0,0,0.75);
  -webkit-box-shadow: 0px 8px 26px -7px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 8px 26px -7px rgba(0,0,0,0.75);
  
  display: grid;
  grid-template-rows:37% 14% 49%;
  grid-template-columns: 65% 35%;

  grid-template-areas: 
  "t q"
  "li li"
  "tv tv";
}

@media (max-width: 660px){
  #container{
    width: 100%;

    margin-bottom: 0;
  }
}

@media (max-width: 777px){
  #container{
    height: 1100px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
}

/*
@media (max-width: 796px){
  #container{
    grid-template-rows: 40% 16% 49%;
  }
}
*/

ul{
    list-style: none;
}

#texto-div{

    grid-area: t;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    padding: 64px 0 0 60px;

    margin-right: 90px;
}

h1{
    margin-bottom: 52px;

    font-size: 28px;

    padding-top: 5px;
}

ul{
    font-size: 15px;
}

li{
    margin-bottom: 18px;

    line-height: 28px;

    font-weight: 300;

}

b{
    font-weight: bolder;
}

li, svg{
    display: inline-block;
}

#link-div-texto{
    display: none;
}

@media (max-width: 777px){
    ul{
        padding: 5px;
    }

    #link-div-texto{
        display: block;
    }
}
#qrcode-div{
    grid-area: q;

    padding: 54px 60px 0 0;
}

#qrcode-image{
    width: 264px;
    height: 264px;

    float: right;
}

@media (max-width: 777px){
    ul{
        padding: 5px;
    }

    #qrcode-div{  
        padding: 50px 0 20px;
    }
}
a{
    color: #009688;
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
}

#link-input-div{
    grid-area: li;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    padding: 10px 60px;
}

#link-div, #input-div{
    display: -webkit-inline-flex;
    display: inline-flex;

    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
}

input{
    margin-right: 6px;
}

label{
    font-size: 14px;

    font-weight: 400;

    color: rgba(35, 33, 59, 0.473)
}

@media (max-width: 777px){
    #link-div{
        display: none;
    }
}
#tutorial-video-div{
    grid-area: tv;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    padding: 72px 60px;
}

#tutorial-video-image{
    width: 560px;
    height: 314px;
}

@media (max-width: 777px){
    #tutorial-video-image{
        width: 100%;
        height: 100%;
    }

    #tutorial-video-div{
        width: 100%;
        height: 100%;

        padding: 0;
    }
}
